import {IonAlert} from '@ionic/react';
import React, {useState} from 'react';
import * as FIcon from 'react-feather';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import './AdminCliente.css';
import {Page, SDataTable} from "../../components";
import {RouteComponentProps} from "react-router";
import {PreguntaFrecuente, Rubro} from "../../services/api";
import {protectedRef, useApiToaster} from "../../helpers-ts";


export const AdminPreguntasFrecuentes: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const [showAlert, setShowAlert] = useState(false);
  const [PreguntaEliminar, setPreguntaEliminar] = useState<PreguntaFrecuente | null>(null)
  const [PreguntaEditar, setPreguntaEditar] = useState<PreguntaFrecuente | null>(null)
  const [showModalEditar, setShowModalEditar] = useState(false);
  const [tituloModalEditar, setTituloModalEditar] = useState('');

  const DataTable = React.createRef<any>()
  const apiToaster = useApiToaster()

  const doEditar = (pregunta: string, respuesta: string) => {
    if (PreguntaEditar){
      PreguntaEditar.pregunta = pregunta
      PreguntaEditar.respuesta = respuesta
      const dtable = DataTable.current
      rootStore.environment.api.guardarPreguntaFrecuente(PreguntaEditar).then((result: any) => {
        apiToaster(result, "Pregunta guardada exitosamente")
        dtable.refresh()
      })
    }
  }

  const handleEditar = (row: any) => {
    setPreguntaEditar(row)
    setShowModalEditar(true)
    setTituloModalEditar(row.id === undefined ? "Crear pregunta" : "Modificar pregunta")
  }

  const handleEliminar = (row: any) => {
    setPreguntaEliminar(row)
    setShowAlert(true)
  };

  const eliminarPregunta = () => {
    if (PreguntaEliminar) {
      const dtable = DataTable.current
      rootStore.environment.api.eliminarPreguntaFrecuente(PreguntaEliminar.id).then((result: any) => {
        apiToaster(result, "Pregunta eliminada exitosamente")
        dtable.refresh()
      })
    }
  }

  const renderAcciones = (row: any) => {
    return <div>
      <span className={'button ml-3'} onClick={() => {
        handleEditar(row)
      }}><FIcon.Edit size={14}/></span>
      <span className={'button ml-3'} onClick={() => {
        handleEliminar(row)
      }}><FIcon.Trash size={14}/></span>
    </div>
  }

  const list2paged_response = (lista: any[]) => {

    return {
      kind: 'ok',
      data: {
        count: lista.length,
        // @ts-ignore
        results: lista.results
      }
    }
  }

  const obtener_preguntas = async (perPage: number, page: number, order: any, search: string) => {
    let respuesta = await rootStore.environment.api.preguntasFrecuentes()
    return list2paged_response(respuesta.kind === 'ok' ? respuesta.preguntas : [])
  }

  return (
    <Page login_required admin>
      <div className="page-wrapper" style={{display: "flex", height: "100vh", overflowY: "scroll"}}>
        <IonAlert
            isOpen={showModalEditar}
            onDidDismiss={() => setShowModalEditar(false)}
            cssClass='alert-preguntas-frecuentes'
            header={tituloModalEditar}
            inputs={[
              {
                name: 'Pregunta',
                type: 'text',
                placeholder: 'Pregunta',
                value: PreguntaEditar?.pregunta,
              },
              {
                name: 'Respuesta',
                type: 'textarea',
                placeholder: 'Respuesta',
                value: PreguntaEditar?.respuesta,
                attributes: {rows: 10}
              },
              ]}
            buttons={[
              {
                text: 'Cancelar',
                role: 'cancel',
                cssClass: 'btn-secondary',
              },
              {
                text: 'Guardar',
                cssClass: 'btn-primary',
                handler: (data) => {
                  doEditar(data.Pregunta, data.Respuesta)
                }
              }
            ]}
        />
        <IonAlert
          isOpen={showAlert}
          onDidDismiss={() => setShowAlert(false)}
          header={'Eliminar pregunta'}
          message={`Confirma que desea eliminar la pregunta "${PreguntaEliminar?.pregunta}"?`}
          buttons={[
            {
              text: 'Cancelar',
              role: 'cancel',
              cssClass: 'btn-secondary',
            },
            {
              text: 'Eliminar',
              cssClass: 'btn-primary',
              handler: () => {
                eliminarPregunta();
              }
            }
          ]}
        />
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h3 className="text-themecolor">Preguntas frecuentes</h3>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item"><a onClick={() => history.push('/admin/')}>Inicio</a></li>
                <li className="breadcrumb-item active">Preguntas frecuentes</li>
              </ol>
            </div>
          </div>
          <ul className="nav nav-pills p-3 bg-white mb-3 align-items-center">
            <li className="nav-item ml-auto">
              <button className="btn btn-primary d-flex align-items-center px-3" id="add-cliente" onClick={() => {
                handleEditar({ pregunta: null, respuesta: null})
              }}>
                Crear pregunta
              </button>
            </li>
          </ul>
          <div className="card mb-0">
            <div className="card-body">
              <div className="table-responsive">
                <SDataTable
                  ref={protectedRef(DataTable)}
                  subHeader
                  obtenerDatos={obtener_preguntas}
                  disablePagination={true}
                  columns={[
                    {
                      name: 'ID',
                      selector: 'id',
                      sortable: false,
                    },
                    {
                      name: 'Pregunta',
                      selector: 'pregunta',
                      sortable: false,
                    },
                    {
                      name: 'Acciones',
                      sortable: false,
                      cell: (row: any) => renderAcciones(row),
                    },
                  ]}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  );
});

export default AdminPreguntasFrecuentes;
